import React, { Component } from 'react';

import { NavLink } from 'react-router-dom'

export default class ArtModal extends Component {
  constructor() {
    super();
    this.state = {    
      zoom : false,
      wall : false,
      liked : false,
      wallColor : '#FFFFFF',
    }

    this.handleLike = this.handleLike.bind(this);
    this.handleZoom = this.handleZoom.bind(this);
  }
  handleLike() {
    this.state.liked ?
      this.setState({ liked : false }) :
      this.setState({ liked : true })
  }
  handleZoom() {
    console.log('Hi');
    this.state.zoom ?
      this.setState({ zoom : false }) :
      this.setState({ zoom : true })
  }
  componentWillUnmount() {
    this.setState({ zoom : false, wall : false, })
  }
  render() {
    return(
        <div className="modal fade custom-modal painting" id="artSelected" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
          <div className={`modal-dialog modal-lg ${this.state.wall ? 'd-none' : ''}`} role="document">
            <div className="modal-content bg-light border-dark shadow-dark">
              <div className="modal-header align-items-center py-4 px-5">
                <div className="modal-title d-flex align-items-center">
                  <div className="title-img mr-4">
                    <img className="rounded-circle" src={require('../assets/images/artist.jpg')} height="35px" width="35px" />
                  </div>
                  <div>
                    <p className="mb-0 font-weight-bold font-size-large">Art Name</p>
                    <p className="mb-0" style={{ color : '#9B9B9B' }}>by <a className="font-weight-bold cursor-pointer" style={{ color : '#4A90E2' }}>Anand Mehta</a> on September 23, 2014</p>
                  </div>
                </div>
                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true"><i className="ion ion-ios-close" style={{ fontSize: '1.6rem' }}></i></span>
                </button>
              </div>
              <div className="modal-body position-relative bg-white d-flex flex-column align-items-center">
                <div className={`p-5 ${this.state.zoom ? 'w-100' : ''}`}>
                  <div className={`border-black p-1 shadow-card ${this.state.zoom ? 'cursor-zoom-out w-100' : 'cursor-zoom-in'}`} onClick={this.handleZoom}>
                    <img className={`${this.state.zoom ? 'w-100 h-auto' : ''}`} src={this.props.image} height="300px" />
                  </div>
                </div>
                <div
                  className={`position-absolute border-dark bg-white rounded p-3 align-items-center justify-content-center flex-column cursor-pointer ${this.state.zoom ? 'd-none' : 'd-flex'}`}
                  onClick={() => this.setState({ wall : true })}
                  style={{ top: '1rem', right: '1rem' }}>
                  <i className="icon ion-md-eye font-size-xlarge"></i>
                  <p className="text-center mb-0">Preview on<br/>wall</p>
                </div>
              </div>
              <div className="modal-footer pb-5 px-5 pt-0">
                <div className="container-fluid">
                  <div className="row">
                    <div className="col-12 d-flex translateY-50">
                      <div
                        className={`rounded-circle border-dark cursor-pointer d-flex align-items-center justify-content-center mr-3 transition ${this.state.liked? 'bg-red text-white':'bg-white'}`}
                        style={{ height: '3rem', width: '3rem' }}
                        onClick={this.handleLike}
                        tabindex="0" data-toggle="tooltip" data-placement="top" title="1,254 People Liked It">
                          <i className="icon font-size-xlarge ion-md-heart transition"></i>
                      </div>
                      <div className="bg-white rounded-circle border-dark cursor-pointer d-flex align-items-center justify-content-center transition" style={{ height: '3rem', width: '3rem' }} tabindex="0" data-toggle="tooltip" data-placement="top" title="Share">
                          <i className="icon font-size-xlarge ion-md-share transition"></i>
                      </div>
                    </div>
                  </div>
                  <div className="row pt-5">
                    <div className="col-md-7">
                      <p className="font-weight-bold font-size-large mb-3">3 Responses</p>
                      <div className="borderbottom-light d-flex py-4">
                        <div className="image">
                          <img className="rounded-circle mr-3" src={require('../assets/images/artist.jpg')} width="25px" height="25px" />
                        </div>
                        <div>
                          <p className="mb-0 font-weight-bold" style={{ fontSize: 0.9 + 'rem' }}>John Carter</p>
                          <p className="mb-2" style={{ fontSize: 0.9 + 'rem' }}>Beautiful Work. Fine Strokes, Would love to have it.</p>
                          <p className="mb-0" style={{ color: '#888', fontSize: 0.8 + 'rem' }}>about 2 hours ago</p>
                        </div>
                      </div>
                      <div className="borderbottom-light d-flex py-4">
                        <div className="image">
                          <img className="rounded-circle mr-3" src={require('../assets/images/artist.jpg')} width="25px" height="25px" />
                        </div>
                        <div>
                          <p className="mb-0 font-weight-bold" style={{ fontSize: 0.9 + 'rem' }}>Reema Bhatiya</p>
                          <p className="mb-2" style={{ fontSize: 0.9 + 'rem' }}>Beautiful Work. Fine Strokes, Would love to have it. You are a fine Artist. Loved it!</p>
                          <p className="mb-0" style={{ color: '#888', fontSize: 0.8 + 'rem' }}>about 2 hours ago</p>
                        </div>
                      </div>
                      <div className="d-flex py-4">
                        <div className="image">
                          <img className="rounded-circle mr-3" src={require('../assets/images/artist.jpg')} width="25px" height="25px" />
                        </div>
                        <div>
                          <p className="mb-0 font-weight-bold" style={{ fontSize: 0.9 + 'rem' }}>Balbinder Singh</p>
                          <p className="mb-2" style={{ fontSize: 0.9 + 'rem' }}>Beautiful Work. Fine Strokes, Would love to have it.</p>
                          <p className="mb-0" style={{ color: '#888', fontSize: 0.8 + 'rem' }}>about 2 hours ago</p>
                        </div>
                      </div>
                      <p className="font-weight-bold font-size-large mt-4 mb-3">Post Your Comment</p>
                      <textarea className="w-100 p-3 mb-3 form-control" style={{ height: '8rem', fontSize: '0.9rem' }}></textarea>
                      <button className="btn bg-dark text-white font-weight-bold py-3 px-4">Post</button>
                    </div>
                    <div className="col-md-5">
                      <p className="font-weight-bold font-size-large mb-4">More Details</p>
                      <div className="mb-4 d-flex">
                        <div className="tag selected">Canvas</div>
                        <div className="tag selected">Oil Painting</div>
                        <div className="tag selected">Abstract</div>
                      </div>
                      <p className="mb-4" style={{ fontSize: '0.9rem' , lineHeight: '1.4rem' }}>25" x 30" <br/> This is the painting of man on a flight, totally abstract, just write anything ,bluw in color , like ashare and follow me for more updates</p>
                      <p className="font-weight-bold font-size-large">&#8377; 2L</p>
                      <NavLink to="/buy" data-dismiss="modal" aria-label="Close" className="btn bg-dark text-white font-weight-bold py-3 px-5">Buy now</NavLink>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div
            className={`position-absolute d-none ${this.state.wall ? 'd-block' : 'd-none'}`}
            style={{ top: '0', left: '0', right: '0', bottom: '0', backgroundColor: this.state.wallColor }}>
              <img
                className="position-absolute"
                style={{width: '100vw', left: '0', bottom: '0' }}
                src={require('../assets/images/wall-back-dark.png')}/>
              <div
                className={`position-absolute translateX-50 border-black p-2 bg-white shadow-painting`}
                style={{ bottom: '45%', left: '50%'}} >
                <img
                  src={this.props.image}
                  height="250px"
                  />
              </div>
              <div className="wall-color">
                <div className="selected-color d-flex align-items-center">
                  <div className="color" style={{ backgroundColor: this.state.wallColor }} ></div><p className="ml-2 mb-0 font-size-large">Wall Color</p>
                </div>
                <div className="hover">
                  <div className="color" style={{ backgroundColor: '#FFFFFF'}} onClick={() => this.setState({ wallColor : '#FFFFFF'})}></div>
                  <div className="color" style={{ backgroundColor: '#8EC25A'}} onClick={() => this.setState({ wallColor : '#8EC25A'})}></div>
                  <div className="color" style={{ backgroundColor: '#4FA2C8'}} onClick={() => this.setState({ wallColor : '#4FA2C8'})}></div>
                  <div className="color" style={{ backgroundColor: '#C3A750'}} onClick={() => this.setState({ wallColor : '#C3A750'})}></div>
                  <div className="color" style={{ backgroundColor: '#FFC8A9'}} onClick={() => this.setState({ wallColor : '#FFC8A9'})}></div>
                  <div className="color" style={{ backgroundColor: '#C31019'}} onClick={() => this.setState({ wallColor : '#C31019'})}></div>
                  <div className="color" style={{ backgroundColor: '#60AD87'}} onClick={() => this.setState({ wallColor : '#60AD87'})}></div>
                  <div className="color" style={{ backgroundColor: '#FE5727'}} onClick={() => this.setState({ wallColor : '#FE5727'})}></div>
                  <div className="color" style={{ backgroundColor: '#C8C7B6'}} onClick={() => this.setState({ wallColor : '#C8C7B6'})}></div>
                  <div className="color" style={{ backgroundColor: '#90FFFF'}} onClick={() => this.setState({ wallColor : '#90FFFF'})}></div>
                  <div className="color" style={{ backgroundColor: '#593FFD'}} onClick={() => this.setState({ wallColor : '#593FFD'})}></div>
                  <div className="color" style={{ backgroundColor: '#FFB550'}} onClick={() => this.setState({ wallColor : '#FFB550'})}></div>
                  <div className="color" style={{ backgroundColor: '#EF43FF'}} onClick={() => this.setState({ wallColor : '#EF43FF'})}></div>
                  <div className="color" style={{ backgroundColor: '#EFFF9E'}} onClick={() => this.setState({ wallColor : '#EFFF9E'})}></div>
                  <div className="color" style={{ backgroundColor: '#9EFFF0'}} onClick={() => this.setState({ wallColor : '#9EFFF0'})}></div>
                  <div className="color" style={{ backgroundColor: '#FFD1EF'}} onClick={() => this.setState({ wallColor : '#FFD1EF'})}></div>
                  <div className="color" style={{ backgroundColor: '#D7D1FF'}} onClick={() => this.setState({ wallColor : '#D7D1FF'})}></div>
                  <div className="color" style={{ backgroundColor: '#D1FFF2'}} onClick={() => this.setState({ wallColor : '#D1FFF2'})}></div>
                  <div className="color" style={{ backgroundColor: '#495057'}} onClick={() => this.setState({ wallColor : '#495057'})}></div>
                  <div className="color" style={{ backgroundColor: '#343a40'}} onClick={() => this.setState({ wallColor : '#343a40'})}></div>
                  <div className="color" style={{ backgroundColor: '#212529'}} onClick={() => this.setState({ wallColor : '#212529'})}></div>

                </div>
              </div>
              <div
                className="position-absolute cursor-pointer bg-white rounded-circle shadow-dark d-flex align-items-center justify-content-center"
                onClick={() => this.setState({ wall : false })}
                style={{ right: '2rem', top: '2rem', height: '4.5rem', width: '4.5rem', fontSize: '1.5rem' }}>
                <i className="ion ion-md-close" />
              </div>
          </div>
        </div>
    );
  }
}

        


