import React, { Component } from 'react';

import { NavLink } from 'react-router-dom';


export default class Navbar extends Component {
	render() {
		return(
			<div className="custum-nav d-flex justify-content-center bg-white shadow-light borderbottom-dark fixed-top w-100 py-2">
				<nav className="navbar navbar-light navbar-expand-lg px-3 w-100 w-md-auto px-md-0 custom-container">
				  <NavLink
				  	to="/" exact activeClassName="navbar-brand pl-3 font-secondary"
				  	className="navbar-brand pl-3 font-secondary"
				  	style={{ fontSize: '1.5rem'}}>
				  	The Art Affair
				  	{/*<img src={require('../assets/images/logo2.png')} height={40}/>*/}
				  </NavLink>
				  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
				    <span className="navbar-toggler-icon"></span>
				  </button>

					<div className="d-lg-none" style={{ flexBasis: '100%'}}>
					  <div className="collapse navbar-collapse" id="navbarSupportedContent">
					  	<div className="py-5 px-3 font-size-xlarge font-weight-bold d-flex flex-column align-items-center">
					  		<div className="py-3">Paintings</div>
					  		<div className="py-3">Upload Your Work</div>
					  		<div className="py-3">Login</div>
					  		<div className="py-3">SignUp</div>
					  	</div>
					  </div>
					</div>

				  <div className="collapse navbar-collapse">
				    <ul className="navbar-nav mr-auto">
				      <li className="nav-item ml-3 dropdown">
				        <NavLink to="/" exact activeClassName="nav-link py-4 px-3 font-size-large active" className="nav-link py-4 px-3 font-size-large">
				          Paintings
				        </NavLink>
				        <div className="dropdown-menu shadow-light border-dark">
				          <NavLink to="/" activeClassName="dropdown-item font-size-large py-2" className="dropdown-item font-size-large py-2">Popular Paintings</NavLink>
				          <NavLink to="/" activeClassName="dropdown-item font-size-large py-2" className="dropdown-item font-size-large py-2">Trending Paintings</NavLink>
				          <NavLink to="/" activeClassName="dropdown-item font-size-large py-2" className="dropdown-item font-size-large py-2">Paintings from Followings</NavLink>
				        </div>
				      </li>
				      <li className="nav-item ml-3">
				        <NavLink to="/help" exact activeClassName="nav-link py-4 px-3 font-size-large active" className="nav-link py-4 px-3 font-size-large">
				          Need Help?
				        </NavLink>
				      </li>
				      {/*<li className="nav-item ml-3 dropdown">
				        <a className="nav-link py-4 px-3 font-size-large">
				          Artists
				        </a>
				        <div className="dropdown-menu shadow-light border-dark">
				          <a className="dropdown-item font-size-large py-2">Popular Artist</a>
				          <a className="dropdown-item font-size-large py-2">Trending Artist</a>
				          <div className="dropdown-divider"></div>
				          <a className="dropdown-item font-size-large py-2">Search For Artist</a>
				        </div>
				      </li>*/}
				      {
				      	this.props.authorise ?
				      	<li className="nav-item ml-3">
					        <NavLink to="/upload" activeClassName="nav-link py-4 px-3 font-size-large active" className="nav-link py-4 px-3 font-size-large">Upload Your Artwork</NavLink>
					    </li> :
				      	null
				      }
				    </ul>
				    {
				    	this.props.authorise ?
				    	<div class="my-2 my-lg-0">
					    	<ul class="navbar-nav mr-auto">
							      <li class="nav-item ml-3 dropdown">
							        <a class="nav-link py-4 px-3 font-size-large">
							          Proksh Luthra <i class="ion ion-ios-arrow-down ml-2"></i>
							        </a>
							        <div class="dropdown-menu shadow-light border-dark">
							          <a class="dropdown-item font-size-large py-2">My Profile</a>
							          <a class="dropdown-item font-size-large py-2">Edit Profile</a>
							          <div class="dropdown-divider"></div>
							          <a class="dropdown-item font-size-large py-2" onClick={this.props.unauthorise}>Log Out</a>
							        </div>
							      </li>
							  </ul>
					    </div> :
				    	<div className="form-inline my-2 my-lg-0">
					      <a className="nav-link py-4 px-5 font-size-large cursor-pointer text-black" data-toggle="modal" data-target="#login">Log in</a>
					      <button className="btn bg-dark text-white px-5 py-3 font-size-large font-weight-bold" data-toggle="modal" data-target="#signup">Sign Up</button>
					    </div>
				    }
				  </div>
				</nav>
			</div>
		);
	}
}


